.game {
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0)), url(/nickel-trivia/static/media/blanket.2eebbff3.jpg) center center no-repeat;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0)), url(/nickel-trivia/static/media/blanket.2eebbff3.jpg) center center no-repeat;
  background-size: cover;
  height: 100vh;
}

.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.choose-text {
  font-size: 2em;
  margin-top: 50px;
  font-family: 'Chelsea Market', cursive;
  color: #0d2d1d;
}

.game-header {
  text-align: center;
  color: #e76f1c;
  font-family: 'Spicy Rice', cursive;
  margin-top: 10px;
  font-size: 5em;
}

.game-wrapper {
  display: grid;
  grid-template-columns: 30% 70%;
}

.jar-logo-game {
  height: 235px;
}

.score {
  display: flex;
  flex-direction: row;
  margin: 10px;
}

.nickel-pic {
  width: 84px;
  position: absolute;
  top: 20px;
  left: 89%;
}

.score-text {
  font-family: 'Chelsea Market', cursive;
  font-size: 2em;
  text-align: center;
  margin-top: 50px;
  z-index: 10;
}

@media screen and (max-width: 1150px) {
  .choose-text {
    font-size: 1.7em;
  }

  .game-header {
    font-size: 4em;
  }

  .question-container {
    font-size: 2rem;
  }
}



@media screen and (max-width: 800px) {
  .choose-text {
    visibility: hidden;
  }

  .game-header {
    font-size: 4em;
    position: absolute;
    left: 20px;
  }

  .score-text {
    font-size: 1.5em;
  }

  .nickel-pic {
    top: 10px;
  }

  .game-wrapper {
    display: flex;
    flex-direction: column;
  }

  .categories-container {
    display: grid;
  }

  .answer-choice {
    font-size: 1em;
  }
}

@media screen and (max-width: 500px) {
  .header-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
  }
}

@media screen and (max-width: 400px) {
  .game-header {
    font-size: 3em;
    position: absolute;
  }

}




.question-container {
  padding: 2rem 4rem;
  display: flex;
  border: 3px solid #293d31;
  font-size: 2.5rem;
  font-family: 'Chelsea Market', cursive;
  border-radius: 2% 6% 5% 4% / 1% 1% 2% 4%;
  background: #fffdf7;
  position: relative;
  margin: 0px 10px 5px 10px;
  text-align: center;
  height: 230px;
  overflow-y: scroll;
  box-shadow: 1px 7px 15px 5px rgba(0,0,0,0.5);
}
      
.question  {
  color: #f76700;
  margin: auto;
}

.answer-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.answer-choice {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 1.8em;
  border: solid black 2px;
  height: 121px;
  padding: 25px 20px 0px;
  font-family: 'Chelsea Market', cursive;
  color: #ffffff;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  overflow-y: scroll;
  border-radius: 10% 6% 5% 4% / 9% 6% 7% 2%;
  margin: 2px;
  box-shadow: 1px 7px 15px 5px rgba(0,0,0,0.5);
}

.answer-choice:hover {
  color: #0e83cd;
  background: #fff;
}

.choice-a {
  background-color: #a9488c;
  margin-left: 10px;
}

.choice-b {
  background-color: #9dd29a;
  margin-right: 10px;
}

.choice-c {
  background-color: #00d1e1;
  margin-left: 10px;
}

.choice-d {
  background-color: #ba2343;
  margin-right: 10px;
}

.answer-response {
  width: 475px;
  height: 300px;
  position: absolute;
  top: 250px;
  left: 50%;
  background-color: #f4dc68;
  border-radius: 2% 6% 5% 4% / 1% 1% 2% 4%;
  border: 3px solid #293d31;
  box-shadow: 7px 14px 19px 9px rgba(0,0,0,0.5)
}

.daily-answer-response {
 left: 50%;
 margin-left: -235px
}

.answer-text {
  font-size: 1.8em;
  text-align: center;
  margin-top: 30px;
  font-family: 'Chelsea Market', cursive;
  padding: 10px;
}

.correct {
  font-size: 1.6em;
}

.nickel-logo-answer {
  width: 100px;
  position: relative;
  left: 200px;
}

.answer-response-hidden {
  visibility: hidden;
}

.error-question-message {
  padding: 2rem 4rem;
  display: flex;
  border: 3px solid #293d31;
  font-size: 2.5rem;
  font-family: 'Chelsea Market', cursive;
  border-radius: 2% 6% 5% 4% / 1% 1% 2% 4%;
  background: #fffdf7;
  position: relative;
  margin: 0px 10px 5px 10px;
  text-align: center;
  height: 230px;
  overflow-y: scroll;
  box-shadow: 1px 7px 15px 5px rgba(0,0,0,0.5);
}

@media screen and (max-width: 1000px) {
  .answer-response {
    left: 30%;
  }

  .daily-answer-response {
   left: 50%;
   margin-left: -235px
  }
}

@media screen and (max-width: 800px) {
  .answer-response {
    top: 750px;
    left: 20%;
  }

  .daily-answer-response {
   top: 300px;
   left: 50%;
   margin-left: -235px
  }
}

@media screen and (max-width: 700px) {
  .answer-response {
    width: 400px;
    height: 300px;
    top: 750px;
    left: 20%;
  }

  .daily-answer-response {
   top: 300px;
   left: 50%;
   margin-left: -235px
  }

  .nickel-logo-answer {
    width: 90px;
    left: 150px;
  }

  .question-container {
    font-size: 2em;
  }

  .answer-text {
    font-size: 1.5em;
    margin-top: 20px;
  }
}

@media screen and (max-width: 400px) {
  .question-container {
    font-size: 1.5em;
    padding: 1rem 1rem;
  }

  .answer-response {
    width: 250px;
    height: 300px;
    position: absolute;
    top: 750px;
    left: 10%;
  }
  .daily-answer-response {
    width: 200px;
    top: 300px;
    left: 80%;
  }

  .correct {
    font-size: 1.2em;
  }

