.categories {
  display: flex;
  flex-direction: column;
}

.category {
  border: black 2px solid;
  font-family: 'Chelsea Market', cursive;
  font-size: 1.6em;
  color: #651a23;
  background: #f6e297;
  cursor: pointer;
  padding: 5px 30px;
  display: inline-block;
  margin: 3px 0px 0px 10px;
